import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';

import AppStore from "../../stores/AppStore";
import GraphStore from "../../stores/GraphStore";
import RightMenuStore from "../../stores/RightMenuStore";
import LegendStore from "../../stores/LegendStore";
import GraphInfoStore from "../../stores/GraphInfoStore";

import './rightMenu.css';

import { rmenuIconsLight, rmenuIconsDark } from '../../utils/rmenuIcons';
import { menuIconsLight, menuIconsDark } from '../../utils/menuIcons';

export interface RightMenuProps { 
  appStore?: AppStore; 
  rightMenuStore?: RightMenuStore;
  graphStore?: GraphStore;
  legendStore?: LegendStore;
  graphInfoStore?: GraphInfoStore;
}

@inject("rightMenuStore", "appStore", "graphStore", "legendStore", "graphInfoStore")
@observer class RightMenu extends Component<RightMenuProps, {}> { 
  render() {
    const rightMenuStore: RightMenuStore = this.props.rightMenuStore;
    const appStore: AppStore = this.props.appStore;
    const graphStore: GraphStore = this.props.graphStore;
    const legendStore: LegendStore = this.props.legendStore;
    const graphInfoStore: GraphInfoStore = this.props.graphInfoStore;
    
    const rmenuIcons: any = appStore.theme === 'dark' ? rmenuIconsLight : rmenuIconsDark;
    const menuIcons: any = appStore.theme === 'dark' ? menuIconsLight : menuIconsDark;
    
    const mode: number = graphStore.mode === 1 ? 0 : 1;

  	return (
			<div className={ `right-menu ${appStore.theme} ${rightMenuStore.open ? 'open' : ''}` }>
        <div className={`right-menu__item ${graphInfoStore.open ? 'active' : ''}`} onClick={graphInfoStore.changeInfoVisible} >
          <span className="right-menu__image"><img src={rmenuIcons.icon10} alt="" /></span>
          <span className="right-menu__title">Описание партии</span>
		    </div>
		    <div className="right-menu__item" >
          <span className="right-menu__image"><img src={rmenuIcons.icon1} alt="" /></span>
          <span className="right-menu__title">Раскрыть\свернуть посреднические цепочки</span>
		    </div>
		    <div className={`right-menu__item ${graphStore.valueVisible ? 'active' : ''}`} onClick={graphStore.changeValueVisible} >
          <span className="right-menu__image"><img src={rmenuIcons.icon2} alt="" /></span>
          <span className="right-menu__title">Отображать\скрыть остатки на узлах</span>
		    </div>
		    <div className={`right-menu__item ${graphStore.labelVisible ? 'active' : ''}`} onClick={graphStore.changeLabelVisible} >
          <span className="right-menu__image"><img src={rmenuIcons.icon3} alt="" /></span>
          <span className="right-menu__title">Отображать\скрыть обороты на ребрах</span>
		    </div>
		    <div className={`right-menu__item ${graphStore.cloneMode ? 'active' : ''}`} onClick={graphStore.changeCloneMode} >
          <span className="right-menu__image"><img src={rmenuIcons.icon4} alt="" /></span>
			 	  <span className="right-menu__title">Отображать\скрыть дубли</span>
		    </div>
		    <div className={`right-menu__item ${graphStore.potentialMode ? 'active' : ''}`} onClick={graphStore.changePotentialMode} >
          <span className="right-menu__image"><img src={rmenuIcons.icon5} alt="" /></span>
          <span className="right-menu__title">Отображать\скрыть потенциальных контрагентов</span>
		    </div>
		    <div className={`right-menu__item ${graphStore.mode ? 'active' : ''}`} onClick={ () => {graphStore.changeMode(mode)}} >
          <span className="right-menu__image"><img src={rmenuIcons.icon6} alt="" /></span>
          <span className="right-menu__title">Группировать\разгруппировать узлы</span>
		    </div>
		    <div className={`right-menu__item ${graphStore.zvMode ? 'active' : ''}`} onClick={graphStore.changeZvMode} >
          <span className="right-menu__image"><img src={rmenuIcons.icon7} alt="" /></span>
          <span className="right-menu__title">Отображать\скрыть связанные ЗВ</span>
		    </div>
        <div className={`right-menu__item ${legendStore.open ? 'active' : ''}`} onClick={legendStore.changeLegendVisible} >
          <span className="right-menu__image"><img src={menuIcons.iconMenu2} alt="" /></span>
          <span className="right-menu__title">Легенда</span>
		    </div>
        <div className={`right-menu__item ${graphStore.direction === 0 ? 'active' : ''}`} onClick={graphStore.setHorizontalDirection} >
          <span className="right-menu__image"><img src={menuIcons.iconMenu3} alt="" /></span>
          <span className="right-menu__title">Горизонтальная ориентация</span>
		    </div>
		    <div className={`right-menu__item ${graphStore.direction === 90 ? 'active' : ''}`} onClick={graphStore.setVerticalDirection} >
          <span className="right-menu__image"><img src={menuIcons.iconMenu4} alt="" /></span>
          <span className="right-menu__title">Вертикальная ориентация</span>
		    </div>
        <div className={`right-menu__item active`} onClick={graphStore.exportToExcel} >
          <span className="right-menu__image"><img src={rmenuIcons.icon9} alt="" /></span>
          <span className="right-menu__title">Выгрузка в Excel</span>
		    </div>
			</div>
		);
  }
}

export default RightMenu;
