import React, { Component } from 'react';
import * as go from 'gojs';
import { observer, inject } from 'mobx-react';
import { Scrollbars } from 'react-custom-scrollbars';

import AppStore from "../../stores/AppStore";
import GraphStore from "../../stores/GraphStore";
import InfoCardStore from "../../stores/InfoCardStore";
import { formatDate, formatCurrency, formatNumber } from '../../utils/utils';
import UnifiedClientProxy from '../../utils/UnifiedClientProxy.js';

import './infoCard.css';

import iconClose from '../../icons/icon_close.png';

export interface InfoCardProps { 
  appStore?: AppStore;
  graphStore?: GraphStore;
  infoCardStore?: InfoCardStore;
}

@inject("infoCardStore", "appStore", "graphStore")
@observer class InfoCard extends Component<InfoCardProps, {}> {
	render() {
		const infoCardStore: InfoCardStore = this.props.infoCardStore;
		const appStore: AppStore = this.props.appStore;
		const graphStore: GraphStore = this.props.graphStore;
		
		const graphInfo: any = graphStore.graphInfo;

		const open: string = infoCardStore.open ? 'open' : '';
		const collapsed: string = infoCardStore.collapsed ? 'collapsed' : ''

		const node: go.Node = infoCardStore.selectedNode;
		const link: go.Link = infoCardStore.selectedLink;

		const cardDataItem = (item: string): string | number => {	
			if(node && node.data.node[item]){
				return node.data.node[item];
			}else{
				return null;
			}
		}

		const GenCardItem = ({ value, title, unit }: { value: string | number, title: string, unit?: string }) => {
			if(node && value){
				return(
					<div className="info-card__item">
						<div className="info-card__item-title">{ title }</div>
						<div className="info-card__item-body">{ value } { unit ? unit : null }</div>
					</div>
				);
			}
			return null;
		}

		const CardItem = ({ value, title }: { value: string, title: string }) => {
			if(node && (node.data.node[value] || node.data.node[value] === 0)){
				if(value === 'root' || value === 'leaf'){
					value = node.data.node[value] ? 'Да' : 'Нет';
				}else if(value === 'TYPE'){
					value = graphStore.nodeTypes[node.data.node[value]];
				}else if(value === 'balance' || value === 'balance_br' || value === 'credit' || value === 'debet'){
					value = formatNumber(node.data.node[value]) + ' ' + graphInfo.unit_name + '.';
				}else if(value === 'cost_in'){
					value = formatCurrency(node.data.node[value]) + ' руб.';
				} else{
					value = node.data.node[value];
				}

				return(
					<div className="info-card__item">
						<div className="info-card__item-title">{ title }</div>
						<div className="info-card__item-body">{ value }</div>
					</div>
				);
			}else{
				return null;
			}
		}

		const CardHeader = () => {
			if(node && (type === 'Kazakhstan' || type === 'Armenia' || type === 'Belarus' || type === 'Kyrgyzstan' || type === 'Russia')){
			  return (
			  	<div>
			  		<div className="info-card__header">
				      <div className="info-card__subtitle">
				        Наименование
				      </div>
				      <div className="info-card__title">
				      	{ cardDataItem('short_name') }
				      </div>
				    </div>
				    <div className="info-card__info">
				      <div className="info-card__info-item">
				      	<span>ИНН:</span> { cardDataItem('inn') }
				      </div>
				      <div className="info-card__info-item">
				      	<span>КПП:</span> { cardDataItem('kpp') }
				      </div>
				    </div>
			  	</div>
			  )
			}
			return null;
		}

		const type = node ? node.data.node.business_role : null;

		const Tax = () => {
			if(node && type === 'Russia'){
			  return <CardItem title='Режим налогообложения' value='tax_type_name' />
			}
			return null;
		}

		const Balance = () => {
			if(node && type === 'Russia'){
			  return <CardItem title='Остаток товара, находящегося в собственности НП' value='balance' />
			}
			return null;
		}

		const BalanceBr = () => {
			if(node && type === 'Russia'){
			  return <CardItem title='Остаток товара, участвующего в посреднических операциях' value='balance_br' />
			}
			return null;
		}

		const Rntp = () => {
			if(node && (node.data.node.root || type === 'Out_to_EAEU' || type === 'Disposal' || type === 'Force_Majeure' 
			|| type === 'Sale_In_Retail' || type === 'Export' || type === 'To_Capital' || type === 'Bankruptcy' 
			|| type === 'Confiscat_by_Goverment' || type === 'To_Fixed_Assets' || type === 'Balance_in_RF' || type === 'Out_Fixed_Assets' 
			|| type === 'Out_Capital' || type === 'Return_From_Retail' || type === 'Return_Lost_Goods' || type === 'Confiscat' 
			|| type === 'From_EAEU')){
			  return <GenCardItem title='РНТП' value={ graphInfo.ipt } />
			}
			return null;
		}

		const Credit = () => {
			if(node && (type === 'Balance_in_RF' || type === 'Out_Fixed_Assets' || type === 'Out_Capital' 
			|| type === 'Return_From_Retail' || type === 'Return_Lost_Goods' || type === 'Confiscat')){
			  return <CardItem title='Количество товара, по которому возобновлена прослеживаемость' value='credit' />
			}
			return null;
		}

		const Credit19 = () => {
			if(node && (type === 'From_EAEU')){
			  return <CardItem title='Количество ввезенного товара' value='credit' />
			}
			return null;
		}

		const Debet = () => {
			if(node && (node.data.node.root || type === 'Out_to_EAEU' || type === 'Disposal' || type === 'Force_Majeure' 
			|| type === 'Sale_In_Retail' || type === 'Export' || type === 'To_Capital' || type === 'Bankruptcy' 
			|| type === 'Confiscat_by_Goverment' || type === 'To_Fixed_Assets')){
			  return <CardItem title='Количество товара' value='debet' />
			}
			return null;
		}

		const Price = () => {
			if(node && node.data.node.root){
			  return <CardItem title='Цена товара' value='cost_in' />
			}
			return null;
		}

		const Country = () => {
			if(node && (node.data.node.root || type === 'From_EAEU')){
			  return <CardItem title='Страна отправления' value='co_code_name' />
			}
			return null;
		}

		const CountryTo = () => {
			if(node && (type === 'Out_to_EAEU')){
			  return <CardItem title='Страна назначения' value='co_code_name' />
			}
			return null;
		}

		const Type = () => {
			if(node && (type === 'Out_to_EAEU' || type === 'Disposal' || type === 'Force_Majeure' || type === 'Sale_In_Retail' 
			|| type === 'Export' || type === 'To_Capital' || type === 'Bankruptcy' || type === 'Confiscat_by_Goverment' 
			|| type === 'To_Fixed_Assets' || type === 'Balance_in_RF' || type === 'Out_Fixed_Assets' || type === 'Out_Capital' 
			|| type === 'Return_From_Retail' || type === 'Return_Lost_Goods' || type === 'Confiscat')){
			  return <CardItem title='Наименование вида операции по выбытию:' value='TYPE' />
			}
			return null;
		}

		const LinkCardItem = ({ value, title }: { value: string, title: string }) => {
			if(link && (link.data[value] || link.data[value] === 0)){
				if(value === 'last_doc_date'){
					const date = new Date(+link.data[value]*1000);
					value = formatDate(date);
				}else if(value === 'quanyity'){
					value = formatNumber(link.data[value]) + ' ' + graphInfo.unit_name + '.';
				}else if(value === 'price_min'){
					value = formatCurrency(link.data[value]) + ' руб.';
				} else{
					value = link.data[value];
				}
				return(
					<div className="info-card__item">
						<div className="info-card__item-title">{ title }</div>
						<div className="info-card__item-body">{ value }</div>
					</div>
				);
			}else{
				return null;
			}
		}

		const GoButton = () => {
			if(node){
				return <div className="button info-card__button" onClick = { () => new UnifiedClientProxy().executeFunction('TaxpayerWebGraphScriptModule', 'OpenNode', JSON.stringify({ipt: graphInfo.ipt, inn: node.data.node.inn })) }>Карточка НП по узлу</div>
			}else if(link){
				return <div className="button info-card__button" onClick = { () => new UnifiedClientProxy().executeFunction('TaxpayerWebGraphScriptModule', 'OpenOperations', JSON.stringify({ipt: graphInfo.ipt })) }>Реестр операций по ребру</div>
			}

			return null;
		}

		return (
			<div className={ `info-card ${appStore.theme} ${open} ${collapsed}` }>
		    <Scrollbars autoHide style={{ height: '100%' }}>
			    <div className="info-card__close" onClick = { infoCardStore.setClose }>
			    	<img src={iconClose} alt="" />
			    </div>
			    <div className="info-card__show" onClick = { infoCardStore.changeCollapsed }>
			      
			    </div>
			    <CardHeader/>
			    <div className="info-card__section">
			    	<Tax/>
			    	<Balance/>
			    	<BalanceBr/>
			    	<Rntp/>
			    	<Debet/>
			    	<Credit/>
			    	<Credit19/>
			    	<Price/>
			    	<Country/>
			    	<CountryTo/>
			    	<Type/>
			    	<LinkCardItem title='Количество товара на ребре' value='quanyity' />
			    	<LinkCardItem title='Cтоимость товара на ребре' value='COST_RUB_GROSS' />
			    	<LinkCardItem title='Дата последней операции' value='last_doc_date' />
			    </div>
				<GoButton />
		    </Scrollbars>
		  </div>
		);
	}
}

export default InfoCard;
