const formatDate = (date: any): string => {
	let dd: any = date.getDate();
	if (dd < 10) dd = '0' + dd;

	let mm: any = date.getMonth() + 1;
	if (mm < 10) mm = '0' + mm;

	let yy: any = date.getFullYear() % 100;
	if (yy < 10) yy = '0' + yy;

	return dd + '.' + mm + '.' + yy;
}

const formatCurrency = (value: any): string => {
  return typeof value !== "undefined" ? (+value).toLocaleString('ru-RU', { minimumFractionDigits: 2 }).replace(',', '.') : null;
}

const formatNumber = (value: any): string => {
  return typeof value !== "undefined" ? (+value).toLocaleString('ru-RU').replace(',', '.') : null;
}

const getParams = ():any => {
	return window.location.search
    .replace('?','')
    .split('&')
    .reduce(
        (p,e) => {
            let a = e.split('=');
            p[ decodeURIComponent(a[0])] = decodeURIComponent(a[1]);
            return p;
        },
        {}
    );
}

export { formatDate, formatCurrency, formatNumber, getParams };