import React, { Component, Fragment } from 'react';
import { ReactOverview } from 'gojs-react';

import { observer, inject } from 'mobx-react';
import InputRange from 'react-input-range';

import AppStore from "../../stores/AppStore";
import ZoomStore from "../../stores/ZoomStore";

import './zoom.css';

import iconClose from "../../icons/icon_close.png";
import iconMap from "../../icons/icon_map.png";
import iconMinusLight from "../../icons/icon_minus.png";
import iconMinusDark from "../../icons/icon_minus_dark.png";
import iconPlusLight from "../../icons/icon_plus.png";
import iconPlusDark from "../../icons/icon_plus_dark.png";

export interface ZoomProps { 
  appStore?: AppStore;
  zoomStore?: ZoomStore;
}

@inject("zoomStore", "appStore")
@observer class Zoom extends Component<ZoomProps, {}> {
  render() {
    const zoomStore: ZoomStore = this.props.zoomStore;
    const appStore: AppStore = this.props.appStore;

    const iconPlus: any = appStore.theme === 'dark' ? iconPlusLight : iconPlusDark;
    const iconMinus: any = appStore.theme === 'dark' ? iconMinusLight : iconMinusDark;
    
  	return (
			<Fragment>
				<div className={ `zoom__map ${appStore.theme} ${zoomStore.mapIsOpen && zoomStore.isOpen ? 'map__open' : ''}` }>
					<ReactOverview
						initOverview={zoomStore.initOverview}
				    divClassName="map__overview"
				    observedDiagram={zoomStore.diagram}
					/>
					<div className="map__close" onClick={zoomStore.closeMap}>
			      <img src={iconClose} alt="" />
			    </div>
				</div>
		    <div className={ `zoom ${appStore.theme} ${ zoomStore.isOpen ? 'open' : 'close'} ${zoomStore.mapIsOpen ? 'map__open' : ''}` }>
		    	<div className="zoom__minus" onClick={ () => zoomStore.setZoom(zoomStore.zoom - 5) }>
			      <img src={iconMinus} alt="" />
			    </div>
			    <div className="zoom__scale">
			      <InputRange
			        maxValue={200}
			        minValue={1}
			        value={zoomStore.zoom}
			        onChange={ (value: number) => zoomStore.setZoom(value) } />
			    </div>
			    <div className="zoom__plus" onClick={ () => zoomStore.setZoom(zoomStore.zoom + 5) }>  
			      <img src={iconPlus} alt="" />
			    </div>
			    <div className="zoom__mapicon" onClick={zoomStore.openMap}>
			      <img src={iconMap} alt="" />
			    </div>
			  </div>
		  </Fragment>
		);
  }
}

export default Zoom;
