import { getBackgroundColor, getNodedColor, getDefaultStrokeColor, getNodeStrokeColor, getTextColor } from './getColors';
import * as icons from '../utils/graphIcons';
import { Vertex, Edge } from '../interface';

const getTechIcon = (type: string): string => {
  switch (type) {
    case 'To_Fixed_Assets':
      return icons.techIcons.iconTech24;
    case 'Out_Fixed_Assets':
      return icons.techIcons.iconTech12;
    case 'Export':
      return icons.techIcons.iconTech20;
    case 'Confiscat_by_Goverment':
      return icons.techIcons.iconTech23;
    case 'Balance_in_RF':
      return icons.techIcons.iconTech23;
    case 'Force_Majeure':
      return icons.techIcons.iconTechForceMajeure;
    default:
      return '';
  }
}

const getIntegralColor = (integral: string): string => {
  switch (integral) {
    case 'Not_Defined':
      return '#A3A3A3';
    case 'Low':
      return '#68B900';
    case 'Medium':
      return '#F6C000';
    case 'Hight':
      return '#D70000';
    case 'Eliminated':
      return '#0090E1';
    default:
      return '#A3A3A3';
  }
}

const getFlagIcon = (coCode: number): string => {
  switch (coCode) {
    case 392:
      return icons.flagIcons.iconFlagJapan;
    case 398:
      return icons.flagIcons.iconFlagKazakhstan;
    case 643:
      return icons.flagIcons.iconFlagRussia;
    case 156:
      return icons.flagIcons.iconFlagChina;
    case 191:
      return icons.flagIcons.iconFlagCroatia;
    default:
      return icons.flagIcons.iconFlagJapan;
  }
}

const getValueIcon = (vertex: Vertex): string => {
  let valueIcon: string = icons.valueIcons.iconValue5A6487;

  if (vertex.node.indicators_top) {
    vertex.node.indicators_top.forEach((risk) => {
      if(risk.Code === 201){
        valueIcon = icons.valueIcons.iconValueF2696D;
      }
    });
  }

  return valueIcon;
}

const getBalanceIcon = (balance: number, balanceBr: number): string => {
  if (balance < 0 && balance > -20000) {
    return icons.balanceIcons.iconBalance;
  } else if (balance < -20000) {
    return icons.balanceIcons.iconBalanceKr;
  } else if (balanceBr > 0) {
    return icons.balanceIcons.iconBalancePos;
  } 
  return icons.balanceIcons.iconBalance;
}

const isTech = (vertex: Vertex): boolean => {
  if (vertex.node.category === 'Abstract' && 
      vertex.node.business_role !== 'Out_to_EAEU' &&
      vertex.node.business_role !== 'From_EAEU' &&
      vertex.node.business_role !== 'Declaration') {
    return true;
  }

  return false;
}

const prepareNode = (vertex: Vertex, theme: string, grpahInfo: any, graphStore: any): Vertex => {
  const mappedNode: Vertex = {
    ...vertex,
    inn: vertex.node.inn,
    balance: vertex.node.balance,
    co_code_name: vertex.node.co_code_name,
    opacity: 1,
    valueVisible: graphStore.valueVisible,
    textColor: getTextColor(theme),
    backgroundColor: getBackgroundColor(theme),
    nodeColor: getNodedColor(theme),
    integralColor: getIntegralColor(vertex.node.integral)
  };

  if (isTech(vertex)) {
    mappedNode.category = 'tech';
    mappedNode.image = getTechIcon(vertex.node.business_role);

  } else if (vertex.node.business_role === 'Declaration') {
    mappedNode.category = 'tech_18';
    mappedNode.flagIcon = getFlagIcon(vertex.node.co_code);
    if(+vertex.node.co_code !== +grpahInfo.orig_co_code){
      mappedNode.isTwoCountry = true;
      mappedNode.origCountryIcon = getFlagIcon(+grpahInfo.orig_co_code);
      mappedNode.origCountryName = grpahInfo.orig_co_name;
    }else{
      mappedNode.isTwoCountry = false;
    }
  } else if (vertex.node.business_role === 'Out_to_EAEU') {
    mappedNode.category = 'tech_c';
    mappedNode.flagIcon = getFlagIcon(vertex.node.co_code);
  } else if (vertex.node.business_role === 'From_EAEU') {
    mappedNode.category = 'tech_19';
    mappedNode.flagIcon = getFlagIcon(vertex.node.co_code);
  } else if (vertex.isClone) {
    mappedNode.category = 'clone';
    mappedNode.cloneIcon = icons.cloneIcons.iconClone5A6487

  } else if(vertex.node.business_role === 'Russia'){
    mappedNode.category = 'taxpayer';
    mappedNode.valueIcon = getValueIcon(vertex);
    mappedNode.flagIcon = getFlagIcon(vertex.node.co_code);
  }else{
    mappedNode.category = 'taxpayerNo';
    mappedNode.valueIcon = getValueIcon(vertex);
    mappedNode.flagIcon = getFlagIcon(vertex.node.co_code);
  }

  mappedNode.balanceIcon = getBalanceIcon(vertex.node.balance, vertex.node.balance_br);

  if(vertex.node.balance >= 0){
    mappedNode.isBalanceKr = false;
  }else{
    mappedNode.isBalanceKr = true;
  }

  const strokeColor: string = getNodeStrokeColor(mappedNode, theme);
  const defaultStrokeColor: string = getDefaultStrokeColor(theme);

  mappedNode.strokeColor = strokeColor;
  mappedNode.isDefaultColor = strokeColor === defaultStrokeColor ? true : false;

  let maxTitleLength: number = 21;

  mappedNode.isRisk = false;
  mappedNode.isBalanceRisk = false;
  mappedNode.isSpeedRisk = false;
  mappedNode.isZatovRisk = false;
  mappedNode.isResortRisk = false;

  if (vertex.node.indicators_top) {
    vertex.node.indicators_top.forEach((risk) => {
      if(risk.Code === 201){
        mappedNode.isBalanceRisk = true;
        mappedNode.balanceIcon = icons.balanceIcons.iconBalance;
        mappedNode.balanceRiskCode = 201;
      }else if(risk.Code === 203){
        mappedNode.isBalanceRisk = true;
        mappedNode.balanceIcon = icons.balanceIcons.iconBalancePos;
        mappedNode.balanceRiskCode = 203;
      }else if(risk.Code === 204){
        mappedNode.isRisk = true;
        mappedNode.isSpeedRisk = true;
        mappedNode.riskIcon = icons.riskIcons.iconSpeed;
        mappedNode.speedRiskIcon = icons.riskIcons.iconSpeed;
        mappedNode.riskCode = 204;
       // maxTitleLength = 13;
      }else if(risk.Code === 205){
        mappedNode.isRisk = true;
        mappedNode.isZatovRisk = true;
        mappedNode.riskIcon = icons.riskIcons.iconZatov;
        mappedNode.zatovRiskIcon = icons.riskIcons.iconZatov;
        mappedNode.riskCode = 205;
     //   maxTitleLength = 13;
      }else if(risk.Code === 206){
        mappedNode.isRisk = true;
        mappedNode.isResortRisk = true;
        mappedNode.riskIcon = icons.riskIcons.iconResort;
        mappedNode.resortRiskIcon = icons.riskIcons.iconResort;
        mappedNode.riskCode = 206;
      //  maxTitleLength = 13;
      }
    });
  }
  
  if(vertex.node.short_name){
    mappedNode.Title = vertex.node.short_name.length > maxTitleLength ? vertex.node.short_name.slice(0, maxTitleLength) + '..' : vertex.node.short_name;
  }else{
    mappedNode.Title = '';
  }

  return mappedNode;
}  

const prepareEdge = (edge: Edge, theme: string, graphNodes: Array<Vertex>, graphStore: any): Edge => {

  const startNode: Vertex = graphNodes.find((node) => node.key === edge.from);
  const endNode: Vertex = graphNodes.find((node) => node.key === edge.to);

  const mappedEdge: Edge = {
    ...edge,
    key: edge.orig_id,
    isFromRoot: false,
    reverse: false,
    labelVisible: graphStore.labelVisible,
    startNodeColor: startNode.strokeColor,
    endNodeColor: endNode.strokeColor,
    //strokeColor: getDefaultStrokeColor(theme),
    strokeColor: '#5a6487',
    backgroundColor: getBackgroundColor(theme),
  }

  if(edge.category === 'Broker'){
    mappedEdge.strokeDashArray = [4, 4];
  }

  let labelArrow: string = '↓';

  //if (isTech(startNode) && startNode.node.loc.y !== 0) {
  if (isTech(startNode)) {
    labelArrow = '↑';
    mappedEdge.from = edge.to;
    mappedEdge.to = edge.from;
    mappedEdge.startNodeColor = endNode.strokeColor;
    mappedEdge.endNodeColor = startNode.strokeColor;
    mappedEdge.reverse = true;
  }

  if (startNode.node.loc.y === 0 || (startNode.node.business_role === 'From_EAEU' && graphStore.modeNoResidents)) {
    mappedEdge.isFromRoot = true;
  }

  mappedEdge.label = labelArrow + ' ' + edge.quanyity;
  //mappedEdge.label = '' + edge.quanyity;

  return mappedEdge;
}

export { prepareNode, prepareEdge };