import * as go from 'gojs'; 

const getBackgroundColor = (theme: string): string => theme === 'dark' ? '#1d2336' : '#eef0f5';

const getNodedColor = (theme: string): string => theme === 'dark' ? '#2b3250' : '#fff';

const getGridColor = (theme: string): string => theme === 'dark' ? '#181e2f' : '#f2f4f7';

const getDefaultStrokeColor = (theme: string): string => theme === 'dark' ? '#5a6487' : '#4C8FE4';

const getNodeStrokeColor = (node, theme: string): string => {

	let strokeColor: string = getDefaultStrokeColor(theme);

  if (node.category === 'tech') {
    switch (node.node.business_role) {
      case 'To_Fixed_Assets':
        strokeColor = '#5BB95B';
        break;
      case 'Out_Fixed_Assets':
        strokeColor = '#31ABC4';
        break;
      case 'Export':
        strokeColor = '#859DE0';
        break;
      case 'Confiscat_by_Goverment':
        strokeColor = '#F09456';
        break;
      case 'Balance_in_RF':
        strokeColor = '#F09456';
        break;
      default:
        strokeColor = getDefaultStrokeColor(theme);
    }
  } else if (node.category === 'root') {
    //strokeColor = defaultStrokeColor;
  } else if (node.category === 'taxpayer'){
    if (node.node.indicators_top) {
      node.node.indicators_top.forEach((risk) => {
        if(risk.Code === 201){
          strokeColor = '#F2696D';
        }
      });
    }
  }

  return strokeColor;
 }

  const getEdgeStrokeColor = (edge: any, theme: string): string => {
    let strokeColor = '#5a6487';

    if (edge.vio_flag) {
      strokeColor = '#31abc4';
    }

    if (edge.category === 'potential') {
      strokeColor = '#f2696d';
    }

    if (edge.category === 'clone') {
      strokeColor = '#5bb95b';
    }

    return strokeColor;
  }

  const getTextColor = (theme: string): string => {
    return theme === 'dark' ? '#ffffff' : '#2b3250';
  }

  export { getBackgroundColor, getNodedColor, getGridColor, getDefaultStrokeColor, getNodeStrokeColor, getTextColor, getEdgeStrokeColor };