import { observable, configure, action, decorate } from 'mobx';

configure({ enforceActions: 'observed' });

class RightMenuStore {
	open: boolean = false;

	changeOpen = (): void => {
		this.open ? this.setClose() : this.setOpen();
	}

	setOpen = (): void => {
		this.open = true;
	}
	
	setClose = (): void => {
		this.open = false;
	}	
};

decorate(RightMenuStore, {
	open: observable,
  changeOpen: action,
  setOpen: action,
  setClose: action
});

export default RightMenuStore;