import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css'

import AppStore from "../../stores/AppStore";
import GraphStore from "../../stores/GraphStore";
import ZoomStore from "../../stores/ZoomStore";
import RightMenuStore from "../../stores/RightMenuStore";

import './menu.css';

import dropdownIconLight from "../../icons/dropdown_arrow.png";
import dropdownIconDark from "../../icons/dropdown_arrow_dark.png";
import iconClose from '../../icons/icon_close.png';
import { menuIconsLight, menuIconsDark } from '../../utils/menuIcons';

export interface MenuProps { 
  appStore?: AppStore;
  graphStore?: GraphStore;
  zoomStore?: ZoomStore;
  rightMenuStore?: RightMenuStore;
}

@inject("appStore", "graphStore" ,"zoomStore", "rightMenuStore")
@observer class Menu extends Component<MenuProps, {}> {
	textInput: any = React.createRef();

  render() {
    const appStore: AppStore = this.props.appStore;
    const graphStore: GraphStore = this.props.graphStore;
    const zoomStore: ZoomStore = this.props.zoomStore;
    const rightMenuStore: RightMenuStore = this.props.rightMenuStore;
    
    const menuIcons: any = appStore.theme === 'dark' ? menuIconsLight : menuIconsDark;
    const dropdownIcon: any = appStore.theme === 'dark' ? dropdownIconLight : dropdownIconDark;

    interface Option {
    	value: number;
    	label: string;
    }

    const options: Array<Option> = [
		  { value: 0, label: 'Общий режим' },
		  { value: 1, label: 'Окружение НП по выбранному узлу'}
		];

		const defaultOption: Option = options[graphStore.mode];

		const DropdownArrow = () => {
			return (
				<img src={dropdownIcon} alt="" />
			)
		}

  	return (
	    <div className={ `header ${appStore.theme}` }>
		    <div className="header__left">
		      <div
		      	className={ `header__item active` } 
			      onClick = { rightMenuStore.changeOpen }>
		        <img src={rightMenuStore.open ? iconClose : menuIcons.iconMenu1} alt="" />
		      </div>
		    </div> 
		    <div className="header__center">
		    	<Dropdown 
			    	options={options} 
			    	onChange={ (option: Option) => { graphStore.changeMode(option.value); }} 
			    	value={defaultOption} 
			    	placeholder="Select an option" 
			    	arrowClosed={<DropdownArrow />} 
	 					arrowOpen={<DropdownArrow />}
		    	/>
		    </div>
		    <div className="header__right">
		      <div className="header__item active" onClick = { appStore.changeTheme }>
		        <img src={menuIcons.iconMenu5} alt="" />
		      </div>
		      <div 
		      	className={ `header__item ${zoomStore.isOpen ? 'active' : ''}` }  
		      	onClick={ zoomStore.changeZoomVisible }>
		        <img src={menuIcons.iconMenu6} alt="" />
		      </div>
		      <div className="header__item header__item-zoom" onClick={ () => this.textInput.current.focus() }>
		        <input 
		        	ref={this.textInput} 
		        	type="text" 
		        	value={zoomStore.zoom} 
		        	onChange={ () => zoomStore.setZoom(parseInt(this.textInput.current.value)) } 
		        />
		      </div>
		      <div 
		      	className={ `header__item ${appStore.isFullScreen ? 'active' : ''}` }  
		      	onClick={ appStore.changeFullScreen }>
		        <img src={menuIcons.iconMenu8} alt="" /> 
		      </div>
		    </div>
		  </div>
		);
  }
}

export default Menu;
