import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import Draggable from 'react-draggable';
import { Scrollbars } from 'react-custom-scrollbars';

import AppStore from "../../stores/AppStore";
import RiskCardStore from "../../stores/RiskCardStore";
import GraphStore from "../../stores/GraphStore";

import { formatDate, formatCurrency, formatNumber } from '../../utils/utils';
import UnifiedClientProxy from '../../utils/UnifiedClientProxy.js';


import iconClose from '../../icons/icon_close.png';
import './riskCard.css';

export interface RiskCardProps { 
  appStore?: AppStore;
  riskCardStore?: RiskCardStore;
  graphStore?: GraphStore;
}

@inject("appStore", "riskCardStore", "graphStore")
@observer class RiskCard extends Component<RiskCardProps, {}> {
  render() {
    const appStore: AppStore = this.props.appStore;
    const riskCardStore: RiskCardStore = this.props.riskCardStore;
    const graphStore: GraphStore = this.props.graphStore;
		
		const graphInfo: any = graphStore.graphInfo;

    const RiskRntp = () => {
      return (
        <div className="risk__item">
          <div className="risk__item-title">
            {riskCardStore.connectedRntp.length > 0 ? 'Связанные РНТП:' : ''}
          </div>
          { riskRntpItems }
        </div>
      )
    }

    const riskRntpItems = riskCardStore.connectedRntp.map((rntp, i) => {
      return(
        <div key = { `rntp_${i}` }  className="risk__item-item">{ rntp }</div>
      )
    })

    const ResortRntp = () => {
      return (
        <div className="risk__item">
          <div className="risk__item-title">
            {riskCardStore.resortRntp.length > 0 ? 'Связанные РНТП:' : ''}
          </div>
          { resortRntpItems }
        </div>
      )
    }

    const resortRntpItems = riskCardStore.resortRntp.map((rntp, i) => {
      return(
        <div
          onClick = { () => new UnifiedClientProxy().executeFunction('TaxpayerWebGraphScriptModule', 'OpenGraph', JSON.stringify({ipt: rntp.IPT})) } 
          key = { `rntp_${i}` }  
          className="risk__item-item risk__resort-rntp">
          { rntp.IPT }
        </div>
      )
    })

    const RiskItem = ({ value, title, unit }: { value: string | number, title: string, unit?: string }) => {
      return (
        <div className="risk__item">
          <div className="risk__item-title">{title}</div>
          <div className="risk__item-item">{value} { unit ? unit : null }</div>
        </div>
      )
    }

    const code = riskCardStore.currentRisk ? riskCardStore.currentRisk.Code : 0;

    const InValue = () => {
      if(code === 201 || code === 203 || code === 205){
        return <RiskItem title="Входящее количество" value={formatNumber(riskCardStore.currentRiskNode.data.node.debet)} unit={graphInfo.unit_name+'.'} />
      }

      return null;
    }

    const OutValue = () => {
      if(code === 201 || code === 203 || code === 205){
        return <RiskItem title="Исходящее количество" value={formatNumber(riskCardStore.currentRiskNode.data.node.credit)} unit={graphInfo.unit_name+'.'} />
      }

      return null;
    }

    const Balance = () => {
      if(code === 201 || code === 203 || code === 205 || code === 206){
        return <RiskItem title="Остаток на узле" value={formatNumber(riskCardStore.currentRiskNode.data.node.balance)} unit={graphInfo.unit_name+'.'} />
      }

      return null;
    }

    const MixBalance = () => {
      if(code === 206){
        return <RiskItem title="Остаток по РНТП" value={formatNumber(riskCardStore.currentRisk.MixBalance)} unit={graphInfo.unit_name+'.'} />
      }

      return null;
    }

    const PriceIn = () => {
      if(code === 204){
        return <RiskItem title="Средняя цена покупки" value={formatCurrency(riskCardStore.currentRisk.priceIn)} unit='руб.'/>
      }

      return null;
    }

    const PriceOut = () => {
      if(code === 204){
        return <RiskItem title="Средняя цена продажи" value={formatCurrency(riskCardStore.currentRisk.priceOut)} unit='руб.'/>
      }

      return null;
    }

    const LastDocDate = () => {
      if(code === 204){
        const date = new Date(riskCardStore.currentRiskNode.data.node.last_doc_date*1000);
        return <RiskItem title="Дата последней сделки" value={formatDate(date)} />
      }

      return null;
    }

    const changePotential = () => {
      graphStore.changePotentialMode();
      graphStore.changePotentialVisible(riskCardStore.currentRiskNode, riskCardStore.currentRisk);
    }

    const ShowPotentialButton = ({ text }: { text: string }) => {
      if(code === 201){
        return <div className="button risk__button" onClick = { changePotential }>{text}</div>
      }

      return null;
    }

    let riskIcon;

    if(code === 201 || code === 203){
      riskIcon = riskCardStore.currentRiskNode.data.balanceIcon;
    }else if(code === 204 || code === 205 || code === 206){
      riskIcon = riskCardStore.currentRiskNode.data.riskIcon;
    }

    return (
      <Draggable>
        <div className={ `risk ${ riskCardStore.riskCardIsOpen ? 'open' : 'close'} ${ appStore.theme }` }>
          <Scrollbars autoHide style={{ height: '240px', width: '340px' }}>
            <div className="risk__close" onClick = { () => riskCardStore.closeRiskCard() }>
              <img src={iconClose} alt="" />
            </div>
            <div className="risk__header">
              <div className="risk__image">
                <img src={riskIcon} alt="" />
              </div>
              <div className="risk__info">
                <div className="risk__title">
                  {riskCardStore.currentRisk ? riskCardStore.currentRisk.Name : ''}
                </div>
                <div className="risk__nodetitle">
                  {riskCardStore.currentRiskNode ? riskCardStore.currentRiskNode.data.node.short_name : ''}
                </div>
              </div>
            </div>
            <div className="risk__inn">
              <span>ИНН:</span>{riskCardStore.currentRiskNode ? riskCardStore.currentRiskNode.data.node.inn : ''}
            </div>
            <div className="risk__items">
              <Balance/>
              <InValue/>
              <OutValue/>
              <RiskRntp/>
              <MixBalance/>
              <ResortRntp/>
              <PriceIn/>
              <PriceOut/>
              <LastDocDate/>
              <ShowPotentialButton text = { `${graphStore.currentPotentialRisk && graphStore.currentPotentialRisk.RiskId === riskCardStore.currentRisk.RiskId ? 'Cкрыть' : 'Показать' } потенциальных контрагентов` } />
            </div>
          </Scrollbars>
        </div>  
      </Draggable>
		);
  }
}

export default RiskCard;
