import { Vertex, Edge, Data, PreparedData } from '../interface';

const convertBooleanData = (value: string | boolean): boolean => typeof value !== 'boolean'? value === 'true' : value;
//const converArrayData = (value) => typeof value === 'string' ? JSON.parse(value) : value;

const prepareVertexTypes = (vertex): Vertex => {
	return {
    key: vertex.key,
    orig_id: vertex.orig_id,
    node: {
      short_name: vertex.node.short_name,
      values: vertex.node.values,
      inn: +vertex.node.inn,
      kpp: +vertex.node.kpp,
      category: vertex.node.category,
      business_role: vertex.node.business_role,
      root: convertBooleanData(vertex.node.root),
      leaf: convertBooleanData(vertex.node.leaf),
      loc: {
        x: +vertex.node.loc.x,
        y: +vertex.node.loc.y
      },
      tax_type: +vertex.node.tax_type,
      tax_type_name: vertex.node.tax_type_name,
      co_code: +vertex.node.co_code,
      co_code_name: vertex.node.co_code_name,
      debet: +vertex.node.debet,
      credit: +vertex.node.credit,
      balance: +vertex.node.balance,
      in_br: +vertex.node.in_br,
      out_br: +vertex.node.out_br,
      balance_br: +vertex.node.balance_br,
      cost_in: +vertex.node.cost_in,
      cost_out: +vertex.node.cost_out,
      price_in: +vertex.node.price_in,
      price_out: +vertex.node.price_out,
      krc: +vertex.node.krc,
      damage: +vertex.node.damage,
      price_avg: +vertex.node.price_avg,
      price_min: +vertex.node.price_min,
      price_max: +vertex.node.price_max,
      st_flag: convertBooleanData(vertex.LEAF),
      connected_nodes: vertex.node.connected_nodes,
      indicators_top: vertex.node.indicators_top,
      integral: vertex.node.integral,
      edges_in: vertex.node.edges_in,
      edges_out: vertex.node.edges_out,
      last_doc_date: +vertex.node.last_doc_date
    }
	}
}

const prepareVertexesTypes = (vertexes): Array<Vertex> => {
	return vertexes.map((vertex) => prepareVertexTypes(vertex));
}

const prepareEdgeTypes = (edge): Edge => {
  return{
    orig_id: edge.orig_id,
    from: edge.from,
    to: edge.to,
    category: edge.category,
    edge_type: edge.edge_type,
    out_co_code: +edge.out_co_code,
    out_co_name: edge.out_co_name,
    in_co_code: +edge.in_co_code,
    in_co_name: edge.in_co_name,
    quanyity: +edge.quanyity,
    price_min: +edge.price_min,
    price_max: +edge.price_max,
    broker: convertBooleanData(edge.broker),
    vio_flag: convertBooleanData(edge.vio_flag),
    last_doc_date: +edge.last_doc_date
  }
}

const prepareEdgesTypes = (edges): Array<Edge> => {
  return edges.map((edge) => prepareEdgeTypes(edge));
}

const prepareDataTypes = (data: any): PreparedData => {  
	return {
		vertexes: prepareVertexesTypes(data.nodes),
		edges: prepareEdgesTypes(data.links),
		graph_info: data.graph_info
	}
}

export { prepareDataTypes };